"use client";

import { useState, useEffect } from 'react';
import { Button } from '@/components/ui/button';
import { Alert, AlertDescription } from '@/components/ui/alert';

// Interface para dados de trading
interface TradingData {
  asset: string;
  timeframe: string;
  order: string;
  probability: number;
  schedule: string;
  signalFound: boolean;
  currentPrice?: number;
  analysis: {
    rsi: string;
    macd: string;
    volume: number;
    trend: string;
    change?: number;
    changePercent?: number;
  };
  source?: string;
  timestamp?: number;
  entryTime?: string;
  entryTimeUtcMs?: number;    // ‚úÖ acrescentado para auto-disparo
  entryTimeIsoUtc?: string;   // ‚úÖ opcional
  error?: string;
}

interface RealtimePrice {
  price: number;
  change: number;
  changePercent: number;
  timestamp: number;
}

export default function TradingPage() {
  // Estados do componente
  const [isActive, setIsActive] = useState(false);
  const [tradingData, setTradingData] = useState<TradingData | null>(null);
  const [realtimePrice, setRealtimePrice] = useState<RealtimePrice | null>(null);
  const [isLoading, setIsLoading] = useState(false);
  const [isSearching, setIsSearching] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [currentTime, setCurrentTime] = useState(new Date());
  const [lastSearchAt, setLastSearchAt] = useState<number | null>(null); // controle interno

  // ‚úÖ Fun√ß√£o para corrigir e exibir hor√°rio no formato HH:mm
  const formatToLocalTime = (dateStr: string | undefined) => {
    if (!dateStr) return "";

    // Caso: formato SQL "YYYY-MM-DD HH:mm:ss"
    if (dateStr.includes(" ") && !dateStr.includes("T")) {
      const [, timePart] = dateStr.split(" ");
      const [hour, minute] = timePart.split(":");
      return `${hour.padStart(2, "0")}:${minute.padStart(2, "0")}`;
    }

    // Caso: formato ISO (com T e possivelmente Z)
    const d = new Date(dateStr);
    if (isNaN(d.getTime())) return dateStr;

    return d.toLocaleTimeString([], { hour: "2-digit", minute: "2-digit" });
  };

  // ‚úÖ calcula o alvo (ms) do hor√°rio de entrada do sinal
  const getEntryTargetMs = (td: TradingData): number => {
    if (td.entryTimeUtcMs && Number.isFinite(td.entryTimeUtcMs)) {
      return td.entryTimeUtcMs;
    }
    // fallback: parse "HH:mm" de schedule (hora local do usu√°rio)
    if (td.schedule) {
      const m = td.schedule.match(/^(\d{1,2}):(\d{2})/);
      if (m) {
        const h = parseInt(m[1], 10);
        const mm = parseInt(m[2], 10);
        const now = new Date();
        const target = new Date(now);
        target.setHours(h, mm, 0, 0);
        // se o hor√°rio j√° passou hoje, considera que deve buscar imediatamente
        if (target.getTime() <= now.getTime()) {
          return now.getTime();
        }
        return target.getTime();
      }
    }
    // se n√£o conseguir determinar, busca imediatamente
    return Date.now();
  };

  // Fun√ß√£o para buscar dados de trading
  const fetchTradingData = async (isManualSearch = false) => {
    if (isManualSearch) {
      setIsSearching(true);
    } else {
      setIsLoading(true);
    }
    setError(null);

    try {
      const tz = Intl.DateTimeFormat().resolvedOptions().timeZone || '';
      // ‚úÖ evita cache e envia tz + tf=ALL para considerar M1/M5/M15
      const response = await fetch(`/api/trading?tf=ALL&tz=${encodeURIComponent(tz)}&t=${Date.now()}`, { cache: 'no-store' });
      const data = await response.json();

      // Se n√£o encontrou sinal com confian√ßa suficiente (202)
      if (response.status === 202) {
        setTradingData(null);        // limpa para reativar polling
        setError(null);
        setLastSearchAt(Date.now());
        console.log(`üîç ${data.message} (${data.probability ?? '-'}% < min)`);
        return;
      }

      // Se houve erro na API
      if (!response.ok) {
        setTradingData(null);
        setError(data.error || 'Falha ao conectar com dados reais');
        setLastSearchAt(Date.now());
        return;
      }

      // Sinal encontrado com confian√ßa suficiente
      setTradingData(data);
      setLastSearchAt(Date.now());
      console.log('üéØ SINAL ENCONTRADO:', data);
    } catch (err) {
      setError('Erro de conex√£o com a API');
      setTradingData(null);
    } finally {
      setIsLoading(false);
      setIsSearching(false);
    }
  };

  // Fun√ß√£o para atualizar pre√ßo em tempo real
  const updateRealtimePrice = async () => {
    if (!tradingData) return;

    try {
      // Simular flutua√ß√£o realista do EUR/USD
      const basePrice = tradingData.currentPrice || 1.0850;
      const volatility = 0.0002; // 0.02% de volatilidade por segundo
      const change = (Math.random() - 0.5) * volatility * basePrice;
      const newPrice = basePrice + change;

      const priceData: RealtimePrice = {
        price: parseFloat(newPrice.toFixed(5)),
        change: change,
        changePercent: (change / basePrice) * 100,
        timestamp: Date.now()
      };

      setRealtimePrice(priceData);

      // Atualizar pre√ßo base para pr√≥xima itera√ß√£o
      if (tradingData) {
        setTradingData(prev => prev ? { ...prev, currentPrice: newPrice } : null);
      }

    } catch (err) {
      console.error('‚ùå Erro ao atualizar pre√ßo:', err);
    }
  };

  // Fun√ß√£o para ativar/desativar o sistema
  const handleToggle = () => {
    if (!isActive) {
      setIsActive(true);
      setTradingData(null);
      fetchTradingData();
    } else {
      setIsActive(false);
      setTradingData(null);
      setRealtimePrice(null);
    }
  };

  // Fun√ß√£o para busca manual de sinais
  const handleSearchSignals = () => {
    if (isActive) {
      fetchTradingData(true);
    }
  };

  // Effect para atualiza√ß√£o da hora atual do computador
  useEffect(() => {
    const timeInterval = setInterval(() => {
      setCurrentTime(new Date());
    }, 1000);
    return () => clearInterval(timeInterval);
  }, []);

  // Effect para busca autom√°tica de sinais a cada 10 segundos quando n√£o h√° sinal
  useEffect(() => {
    if (isActive && !tradingData) {
      const interval = setInterval(() => fetchTradingData(false), 10000);
      return () => clearInterval(interval);
    }
  }, [isActive, tradingData]);

  // Effect para atualiza√ß√£o de pre√ßo em tempo real quando h√° sinal
  useEffect(() => {
    if (isActive && tradingData) {
      const priceInterval = setInterval(updateRealtimePrice, 1000); // atualiza a cada 1s
      return () => clearInterval(priceInterval);
    }
  }, [isActive, tradingData]);

  // ‚úÖ Effect: assim que o hor√°rio do sinal "acaba", busca outro automaticamente
  useEffect(() => {
    if (!isActive || !tradingData) return;

    const targetMs = getEntryTargetMs(tradingData);
    const timer = setInterval(() => {
      const now = Date.now();
      if (now >= targetMs) {
        clearInterval(timer);
        // limpa o sinal corrente e dispara nova busca
        setTradingData(null);
        setRealtimePrice(null);
        fetchTradingData(false);
      }
    }, 1000);

    return () => clearInterval(timer);
  }, [isActive, tradingData]);

  return (
    <div className="min-h-screen bg-gradient-to-br from-slate-900 via-blue-900 to-slate-900 text-white">
      {/* Header com logo GPTTRADER */}
      <div className="flex flex-col items-center pt-12 pb-8">
        {/* Logo GPTTRADER */}
        <div className="relative mb-6">
          <img
            src="https://rediecert.com.br/gptimagem/logogpt.png"
            alt="GPTTRADER Logo"
            className="w-60 h-60 mb-4 mx-auto"
            onError={(e) => {
              e.currentTarget.style.display = 'none';
              const nextElement = e.currentTarget.nextElementSibling as HTMLElement;
              if (nextElement) {
                nextElement.style.display = 'block';
              }
            }}
          />
          <div className="w-24 h-24 mb-4 mx-auto hidden">
            <svg viewBox="0 0 100 100" className="w-full h-full">
              <defs>
                <linearGradient id="hexGradient" x1="0%" y1="0%" x2="100%" y2="100%">
                  <stop offset="0%" stopColor="#00bfff" />
                  <stop offset="50%" stopColor="#0080ff" />
                  <stop offset="100%" stopColor="#6600ff" />
                </linearGradient>
                <linearGradient id="gGradient" x1="0%" y1="0%" x2="100%" y2="100%">
                  <stop offset="0%" stopColor="#ffffff" />
                  <stop offset="100%" stopColor="#e0e0e0" />
                </linearGradient>
              </defs>
              <path d="M50 8 L78 25 L78 58 L50 75 L22 58 L22 25 Z"
                    fill="url(#hexGradient)"
                    stroke="#00bfff"
                    strokeWidth="2"/>
              <path d="M35 30 L35 53 L50 53 L50 45 L42 45 M35 30 L50 30"
                    fill="none"
                    stroke="url(#gGradient)"
                    strokeWidth="4"
                    strokeLinecap="round"/>
            </svg>
          </div>
        </div>
        
        <p className="text-cyan-400 text-sm tracking-wider">SISTEMA DE AN√ÅLISE INTELIGENTE</p>
      </div>

      {/* Controles principais */}
      <div className="flex flex-col items-center mb-8">
        <div className="flex gap-4 mb-4">
          <Button
            onClick={handleToggle}
            className={`px-16 py-4 text-xl font-semibold rounded-xl border-2 transition-all duration-300 ${
              isActive
                ? 'bg-red-500 hover:bg-red-600 border-red-400 shadow-lg shadow-red-500/50'
                : 'bg-transparent hover:bg-cyan-500/20 border-cyan-400 text-cyan-400'
            }`}
          >
            {isActive ? 'DESATIVAR' : 'ATIVAR'}
          </Button>

          {/* Bot√£o de busca manual */}
          {isActive && (
            <Button
              onClick={handleSearchSignals}
              disabled={isSearching}
              className="px-8 py-4 text-lg font-semibold rounded-xl border-2 bg-transparent hover:bg-green-500/20 border-green-400 text-green-400 transition-all duration-300"
            >
              {isSearching ? 'BUSCANDO...' : 'BUSCAR SINAIS'}
            </Button>
          )}
        </div>

        {/* Status do sistema */}
        {isActive && (
          <div className="mt-4 text-center">
            <p className="text-cyan-400 text-sm">SISTEMA ATIVO</p>
            {!tradingData && !error && (
              <div className="mt-2">
                <p className="text-yellow-400 text-sm font-semibold">üîç ANALISANDO MERCADO</p>
                <p className="text-xs text-slate-400 mt-1">
                  Aguardando sinal de alta confian√ßa (+80%)
                </p>
                <div className="flex items-center justify-center mt-2">
                  <div className="animate-pulse flex space-x-1">
                    <div className="w-2 h-2 bg-cyan-400 rounded-full"></div>
                    <div className="w-2 h-2 bg-cyan-400 rounded-full animation-delay-200"></div>
                    <div className="w-2 h-2 bg-cyan-400 rounded-full animation-delay-400"></div>
                  </div>
                </div>
              </div>
            )}
            {tradingData?.source && (
              <div className="mt-2">
                <p className="text-green-400 text-sm font-semibold">üéØ SINAL ENCONTRADO</p>
                <p className="text-xs text-slate-400 mt-1">
                  Dados em tempo real
                </p>
              </div>
            )}
          </div>
        )}
      </div>

      {/* Pre√ßo em Tempo Real - SEMPRE VIS√çVEL quando ativo */}
      {isActive && (
        <div className="max-w-4xl mx-auto px-4 mb-8">
          <div className="bg-slate-800/50 border border-cyan-400/30 rounded-lg p-6 text-center">
            <h3 className="text-cyan-400 text-lg mb-4">TEMPO REAL</h3>
            {realtimePrice ? (
              <div>
                <div className="text-4xl font-bold text-white mb-2">
                  {realtimePrice.price.toFixed(5)}
                </div>
                <div className={`text-xl font-semibold ${
                  realtimePrice.changePercent >= 0 ? 'text-green-400' : 'text-red-400'
                }`}>
                  {realtimePrice.changePercent >= 0 ? '‚Üó +' : '‚Üò '}{Math.abs(realtimePrice.changePercent).toFixed(4)}%
                </div>
                <div className="text-sm text-slate-400 mt-2">
                  Varia√ß√£o: {realtimePrice.change >= 0 ? '+' : ''}{realtimePrice.change.toFixed(5)}
                </div>
                <div className="text-xs text-slate-500 mt-1">
                  Atualizado: {new Date(realtimePrice.timestamp).toLocaleTimeString()}
                </div>
              </div>
            ) : tradingData?.currentPrice ? (
              <div>
                <div className="text-4xl font-bold text-white mb-2">
                  {tradingData.currentPrice.toFixed(5)}
                </div>
                <div className="text-slate-400">Iniciando atualiza√ß√µes em tempo real...</div>
              </div>
            ) : (
              <div>
                <div className="text-4xl font-bold text-white mb-2">1.08500</div>
                <div className="text-slate-400">Carregando dados...</div>
              </div>
            )}
          </div>
        </div>
      )}

      {/* Pain√©is de dados - APENAS quando h√° sinal */}
      {isActive && tradingData && (
        <div className="max-w-4xl mx-auto px-4">
          {/* Informa√ß√µes b√°sicas do sinal */}
          <div className="grid grid-cols-1 md:grid-cols-2 gap-4 mb-8">
            <div className="bg-slate-800/50 border border-cyan-400/30 rounded-lg p-4">
              <h3 className="text-cyan-400 text-sm mb-2">ATIVO:</h3>
              <p className="text-white text-lg font-bold">
                {tradingData.asset}
              </p>
            </div>

            <div className="bg-slate-800/50 border border-cyan-400/30 rounded-lg p-4">
              <h3 className="text-cyan-400 text-sm mb-2">TIMEFRAME:</h3>
              <p className="text-white text-lg font-bold">
                {tradingData.timeframe}
              </p>
            </div>

            <div className="bg-slate-800/50 border border-cyan-400/30 rounded-lg p-4">
              <h3 className="text-cyan-400 text-sm mb-2">OPERA√á√ÉO:</h3>
              <p className={`text-lg font-bold ${
                tradingData.order === 'COMPRA' ? 'text-green-400' : 'text-red-400'
              }`}>
                {tradingData.order}
              </p>
            </div>

            <div className="bg-slate-800/50 border border-cyan-400/30 rounded-lg p-4">
              <h3 className="text-cyan-400 text-sm mb-2">HOR√ÅRIO DE ENTRADA:</h3>
              <div className="text-xs text-slate-400 mt-1">
                Hora do computador: {currentTime.toLocaleTimeString([], { hour: "2-digit", minute: "2-digit" })}
              </div>
              <p className="text-white text-lg font-bold">
                {formatToLocalTime(tradingData.entryTime) || formatToLocalTime(tradingData.schedule)}
              </p>
              <p className="text-xs text-slate-400 mt-1">
                Pr√≥xima vela ({tradingData.timeframe})
              </p>
            </div>
          </div>

          {/* Barra de probabilidade */}
          <div className="mb-8">
            <h3 className="text-center text-cyan-400 mb-4">
              N√çVEL DE CONFIAN√áA: {tradingData.probability}%
            </h3>
            <div className="w-full bg-slate-700 rounded-full h-4">
              <div
                className={`h-4 rounded-full transition-all duration-1000 ${
                  tradingData.probability >= 80 ? 'bg-gradient-to-r from-green-400 to-cyan-400' :
                  tradingData.probability >= 70 ? 'bg-gradient-to-r from-yellow-400 to-green-400' :
                  'bg-gradient-to-r from-orange-400 to-yellow-400'
                }`}
                style={{ width: `${tradingData.probability}%` }}
              ></div>
            </div>
            <div className="text-center text-sm text-slate-400 mt-2">
              {tradingData.probability >= 80 ? 'Alta Confian√ßa' :
                tradingData.probability >= 70 ? 'Confian√ßa Moderada' : 'Confian√ßa Baixa'}
            </div>
          </div>

          {/* Status do sinal */}
          <div className="mb-8">
            <Alert className="bg-green-500/20 border-green-400 text-green-100">
              <AlertDescription className="text-center text-lg font-semibold">
                ‚úÖ SINAL ENCONTRADO COM {tradingData.probability}% DE CONFIAN√áA
              </AlertDescription>
            </Alert>
          </div>

        </div>
      )}

      {/* Exibi√ß√£o de erros */}
      {error && isActive && (
        <div className="max-w-4xl mx-auto px-4">
          <Alert className="mt-4 bg-red-500/20 border-red-400 text-red-100">
            <AlertDescription>{error}</AlertDescription>
          </Alert>
        </div>
      )}
    </div>
  );
}
